    this.branchesEnergyBars = new BranchesEnergyBars(scene);

    this.circularProgress.draw({
      x: UI.tileToScreen(19.15),
      y: UI.tileToScreen(1),
      radius: UI.tileToPixels(1.5),
    });
    this.branchesEnergyBars.draw({
      x: UI.tileToScreen(1.5),
      y: UI.tileToScreen(0.5),
    });

    this.drawProfecy();
