  navigateToScene(sceneId, props = {}) {
    this.cameras.main.fade(500, 0, 0, 0);
    this.tweens.add({
      targets: titlesMusic,
      volume: 0,
      ease: "Linear",
      duration: 500,
      onComplete: () => {
        titlesMusic.pause();
        console.log(sceneId);
        this.scene.start(sceneId, props);
      },
    });
  }

